
import IconAvatar from '@/assets/img/svg/icon_avatar'
import IconMenu from '@/assets/img/svg/icon_menu'
import IconGlobal from '@/assets/img/svg/icon_global'
import React, { memo } from 'react'
import { RightWrapper } from './style'
import { useState,useEffect } from 'react'

const HeadRight= memo((props) => {
  const [showPanel,setShowPanel] = useState(false)

  useEffect(()=>{
    function windowHandleClick(){
      setShowPanel(false)
    }
    window.addEventListener('click',windowHandleClick,true)//true

    return ()=>{
      window.removeEventListener('click',windowHandleClick,true)//
    }
  },[])


  function profileHandle(){
    setShowPanel(true)
  }
  return (
 <RightWrapper>
   <div className = "btns">
      <span className='btn'></span>
      <span className='btn'></span>
      <span className='btn'><IconGlobal/></span>
   </div>
    <div className="profile" onClick ={
      profileHandle
    } >
      <IconMenu/>
      <IconAvatar/> 
    </div>
    {showPanel && (
      <div className='panel'>
      <div className='top'>
        <div className='item register'></div>
        <div className='item login'></div>
      </div>
      <div>
        <div className='bottom'>
          <div className='item'></div>
          <div className='item'></div>
          <div className='item'></div>
        </div>
      </div>
    </div>
    )}
 </RightWrapper>
 )
})

export default HeadRight
